<template>
  <div class="rich-text-editor">
    <Hints>
      <template slot="hintName"></template>
      <template slot="hintInfo">
        <p>wangEditor:javascriptcss Web </p>
        <p>wangEditorVueVue</p>
        <p> <el-link type="success" href="http://www.wangeditor.com/" target="_blank">wangEditor</el-link></p>
      </template>
    </Hints>
    <Editor v-model="content" :placeholder="placeholder"/>
  </div>
</template>

<script>
import Hints from '../../components/Hints/index'
import Editor from '../../components/Editor'

export default {
  name: 'RichTextEditor',
  data () {
    return {
      content: '',
      placeholder: '<b style="font-size: 16px">...</b>'
    }
  },
  components: { Editor, Hints }
}
</script>

<style lang="less">

</style>



