<template>
  <div class="editor-wrapper">
    <div class="editor-container" ref="editorEle"></div>
  </div>
</template>

<script>
import E from 'wangeditor'

export default {
  name: 'Editor',
  data () {
    return {
      editor: null
    }
  },
  props: {
    value: {
      type: String,
      default: ''
    },
    placeholder: {
      type: String,
      default: ''
    }
  },
  watch: {
    value (val) {
      this.editor.txt.html(val)
    }
  },
  mounted () {
    this.editor = new E(this.$refs.editorEle)
    this.editor.customConfig.onchange = (html) => {
      this.$emit('input', html)
    }
    this.editor.customConfig.uploadImgServer = ''
    this.editor.customConfig.uploadFileName = ''
    // 
    this.editor.customConfig.uploadImgHooks = {
      before: function (xhr, editor, files) {
        // 
        // xhr  XMLHttpRequst editor files 

        //  {prevent: true, msg: 'xxxx'} 
        // return {
        //     prevent: true,
        //     msg: ''
        // }
      },
      success: function (xhr, editor, result) {
        // 
        // xhr  XMLHttpRequst editor result 
        this.imgUrl = Object.values(result.data).toString()
      },
      fail: function (xhr, editor, result) {
        // 
        // xhr  XMLHttpRequst editor result 
      },
      error: function (xhr, editor) {
        // 
        // xhr  XMLHttpRequst editor 
      },
      timeout: function (xhr, editor) {
        // 
        // xhr  XMLHttpRequst editor 
      },

      //  {errno:0, data: [...]} 
      //  JSON 
      customInsert: function (insertImg, result, editor) {
        // 
        // insertImg editor result 

        //  {url:'....'} 
        // result.data
        let url = Object.values(result.data)
        // JSON
        JSON.stringify(url)
        insertImg(url)
        // result  JSON 
      }
    }

    // 
    this.editor.create()
    this.editor.txt.html(this.placeholder)
  },
  beforeDestroy () {
    this.editor.destroy()
  }
}
</script>

<style lang="less">
  .editor-wrapper{
    .editor-container{
      text-align:left;
      box-shadow: rgba(0, 0, 0, 0.1) 0 2px 12px 0;
      .w-e-toolbar{
        padding: 5px;
        border: none!important;
      }
      .w-e-text-container{
        min-height: 450px !important;
        border: none!important;
        .w-e-text{
          padding: 10px;
          overflow-y: auto;
        }
      }
    }
  }
</style>



